  font-family: "Open Sans", sans-serif;
}

.pageContainer {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
}

.content {
  height: 100vh;
  -webkit-flex: 1 1;
          flex: 1 1;
}

@media only screen and (max-width: 768px) {
  .content {
    height: 100vh;
    -webkit-flex: 3 1;
            flex: 3 1;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: none;
